import React from 'react';
import Chart from 'react-apexcharts'

function ChartLine(props){

    const data ={
        series: [{
            name: "Registros",
            data: props.data1.value
        }],
        option: {
            chart: {
                type: 'area',
                height: 160,
                sparkline: {
                    enabled: true
                },
            },
          stroke: {
                curve: 'straight'
          },
          fill: {
                opacity: 0.3
          },
          xaxis: {
                categories: props.data1.name,
          },
          colors: ['#00cc99']
        },
    }

    return (
      <div>
        <Chart options={data.option} series={data.series} type="area" height={100} />	
      </div>
    );
}

export default ChartLine;