import React, { Component } from 'react';
import Chart from 'react-apexcharts';

class ChartPie extends Component {
	render() {
		const data = {
			series: this.props.data,
			options: {
				  chart: {
					width: 380,
					type: 'pie',
				  },
				  legend: {
					position: 'top'
				},
				labels: this.props.labels,
				responsive: [{
					breakpoint: 480,
					options: {
						chart: {
							width: 200
						},
						legend: {
							position: 'top'
						}
					}
				}]
			},
		  
		};
		
		return (
			<Chart options={data.options} series={data.series} type="pie" height={`${this.props.width}px`} />
		);
	}
}

export default ChartPie;